import React, { Component } from 'react';
import Model from '../../Model';
class index extends Component {
    render() {
        return (
            <div className="container text-center gioHang my-5">
                <h2 className="title text-left"> Gi hng <span>(0 Sn phm)</span></h2>
                <img src="https://salt.tikicdn.com/desktop/img/mascot@2x.png" className="my-3" width={190} alt="test"  />

                {this.props.login == true ?
                    <>
                        <p className="text-center mb-3">Khng c sn phm no trong gi hng ca bn.</p>
                        <a href="/" className="btn btn-warning mt-5">Tip tc mua sm</a>
                    </>
                    :
                    <>
                        <p className="text-center mb-3">Vui lng ng nhp  mua hng.</p>
                      
                            
                            <Model/>
                       
                    </>

                }

            </div>
        );
    }
}

export default index;