import React, { Component } from 'react';
import Swal from 'sweetalert2';
import { ShopBanHangServices } from '../../../Services';

class index extends Component {
    constructor(props) {
        super(props)
        this.state = {
            StoreOwnername: "",
            Phone: "",
            EmailOwner: "",
            PasswordShop: "",
            ShopName: "",
            BusinessLicense: true,
            BusinessRegisCode: 0,
            Country: "",
            CommodityIndustry: ""
        }
    }
    onChange = (e) => {
        const input = e.target
        this.setState({
            [input.name]: input.value
        }, () => {
        })
    }
    onClick = (e) => {
        e.preventDefault()
        ShopBanHangServices.dangKy(this.state).then(res => {
            Swal.fire({
                position: 'center',
                icon: 'success',
                title: "ng k thnh cng ! ",
                showConfirmButton: false,
                timer: 1500
            })
            this.props.history.push('/dangnhapbanhang')
        }).catch(err => {
            Swal.fire({
                position: 'center',
                icon: 'error',
                title: err.response.data.message,
                showConfirmButton: false,
                timer: 1500
            })
        })
    }
    render() {
        return (
            <div className="dangKyBanHang">
                <div className=" bgWhite text-center">
                    <form action="" className="py-4 formDangKy">
                        <h4 className="h3">ng k bn hng cng QT-Data</h4>
                        <p className="mt-2">Cm n i tc  tin tng v la chn ng hnh cng QT-Data!
                    Vui lng hon tt thng tin  to ti khon ng nhp Trung Tm Bn Hng - QT-Data Seller Center.</p>
                        <div className="allForm">
                            <div className="row">
                                <div className="col-12 col-md-6">
                                    <label htmlFor=""><b>H v tn ch ca hng : </b></label>
                                </div>
                                <div className="col-12 col-md-6 ">
                                    <input className="form-control" type="text" name="StoreOwnername" onChange={this.onChange} placeholder="Nguyn Vn A" />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-12 col-md-6">
                                    <label htmlFor=""><b>S in thoi ch ca hng : </b></label>
                                </div>
                                <div className="col-12 col-md-6 ">
                                    <input className="form-control" type="text" name="Phone" onChange={this.onChange} placeholder="0123456789" />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-12 col-md-6">
                                    <label htmlFor=""><b>a ch email ca ch hng : </b></label>
                                </div>
                                <div className="col-12 col-md-6 ">
                                    <input className="form-control" type="text" name="EmailOwner" onChange={this.onChange} placeholder="S dng  ng nhp admin bn hng" />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-12 col-md-6">
                                    <label htmlFor=""><b>Mt khu : </b></label>
                                </div>
                                <div className="col-12 col-md-6 ">
                                    <input className="form-control" type="password" name="PasswordShop" onChange={this.onChange} placeholder="Password" />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-12 col-md-6">
                                    <label htmlFor=""><b>Tn ca hng : </b></label>
                                </div>
                                <div className="col-12 col-md-6 ">
                                    <input className="form-control" type="text" placeholder="Ca hng ca ti" name="ShopName" onChange={this.onChange} />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-12 col-md-6">
                                    <label htmlFor=""><b>Doanh nghip ca bn  c giy php kinh doanh cha ? : </b></label>
                                </div>
                                <div className="col-12 col-md-6 ">
                                    <input className="" type="radio" name="BusinessLicense" value={true} onChange={this.onChange} />
                                    <span className="ml-2"> c</span>
                                    <input className="ml-4" type="radio" name="BusinessLicense" value={false} onChange={this.onChange} />
                                    <span className="ml-2">Cha c</span>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-12 col-md-6">
                                    <label htmlFor=""><b>M s ng k kinh doanh : </b></label>
                                </div>
                                <div className="col-12 col-md-6 ">
                                    <input className="form-control" type="text" name="BusinessRegisCode" onChange={this.onChange} placeholder="M s" />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-12 col-md-6">
                                    <label htmlFor=""><b>Tnh/thnh ph : </b></label>
                                </div>
                                <div className="col-12 col-md-6 ">
                                    <select name="Country" onChange={this.onChange} className=" form-control" id="">
                                        <option value="">Tnh/Thnh ph</option>
                                        <option value="H Ch Minh">H Ch Minh</option>
                                        <option value="H Ni">H Ni</option>
                                    </select>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-12 col-md-6">
                                    <label htmlFor=""><b>Chn ngnh hng : </b></label>
                                </div>
                                <div className="col-12 col-md-6 ">
                                    <select name="CommodityIndustry" onChange={this.onChange} className=" form-control" id="">
                                        <option value="">Chn ngnh hng</option>
                                        <option value="Cng ngh">Cng ngh</option>
                                        <option value="Thi trang">Thi trang</option>
                                    </select>
                                </div>
                            </div>
                        </div>
                        <button className="btn btn-primary mt-4" onClick={this.onClick}>ng k bn hng</button>
                        <p className="my-4">
                            Bng cch gi n ng k ca bn, bn ng  vi <a href="/">Tha thun dch v</a> ca chng ti
                            v xc nhn rng thng tin bn cung cp  hon chnh v chnh xc.
                        </p>
                    </form>
                </div>
            </div>
        );
    }
}

export default index;